#module_range {
  height: 80vh;

  .row {
    height: 80vh;
  }

  .column {
    flex-grow: 1;
    align-content: center;
    justify-content: center;
    height: 100%;
  }

  button {
    flex-basis: 20%;
    width: 25vw;
    height: 25vw;
    margin: auto;
    font-size: 5vw;
  }
}

.range_container {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 48%;
  position: relative;
  /* background-color: white; */
  width: 100%;
  margin: auto;

  .green_zone {
    height: 20px;
    width: 100%;
    background-color: green;
    z-index: 10;
    position: absolute;
  }

  input {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    height: 25vw;
    border: none;
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%) rotate(90deg);
    background-color: transparent;
  }

  input::-webkit-slider-thumb {
    -webkit-appearance: none;
    background-color: orange;
    width: 5vw;
    height: 25vw;
  }
}
